<template>
<div class="addradd">
    <c-top-back></c-top-back>
    <c-group>
        <c-input title=" " @on-change="validate" placeholder="" name="name" v-model="formData.name" required :max="20" ></c-input>
        <c-input title=" " @on-change="validate" placeholder="" name="phone" required v-model="formData.phone" is-type="china-mobile" ></c-input>
            <c-city title="" v-model="formData.addr_code" @getname="getname" @on-hide="logHide" placeholder="" :list="addressData"></c-city>
        <c-input title="" @on-change="validate" placeholder="" required name="address" v-model="formData.address" :min="5" :max="100"></c-input>
        <c-input title="  " @on-change="validate" placeholder="" required name="post_code" v-model="formData.post_code" is-type="number" :min="6" :max="10"></c-input>
    </c-group>
    <div class="wrap-pd" style="margin-top:10px;">
        <c-button text="" :type="color" :disabled="disabled" @click.native="preserve" size="block"></c-button>
    </div>
    
</div>

</template>
<script>
    import utils from '../../libs/utils.js';
    var ChinaAddressData = require("../../libs/components/data/china_address.json");
    export default {
        data (){
            return {
                formData:{               //
                    phone:'',             //
                    name:'',              //                           
                    post_code:'',             //
                    address:'',             //
                    addr_code:[],             //
                    addr_text:[]
                },
                addressData:ChinaAddressData,
                color:'default',            //
                name_status:false,          //
                phone_status:false,             //
                code_status:false,             //
                addr_status:false,             //
                disabled:true,             //
                addr_code_status:false,            //
            }
        },

        methods:{
            preserve(){
                if(!this.disabled){
                    let that = this;
                    utils.ajax({
                        url: "/user/user/address/set",
                        dataType: 'json',
                        type: 'POST',
                        data:{
                            'name':that.formData.name,
                            'phone':that.formData.phone,
                            'post_code':that.formData.post_code,
                            'address':that.formData.address,
                            'province':that.formData.addr_code[0],
                            'city':that.formData.addr_code[1],
                            'county':that.formData.addr_code[2],
                            'str':that.formData.addr_text[0]+' '+that.formData.addr_text[1]+' '+that.formData.addr_text[2]+' '+that.formData.address
                        },
                        success: function(data){
                            if(data.code=="SUCCESS"){
                                    utils.go({path:'/order/add'},that.$router,true); 
                            }else if(data.code=='user_seller_error'){
                                that.$vux.alert.show({content:'',onHide :function(){
                                  utils.go({path:'/home/home',query:{id:sessionStorage.getItem('product_id')}},that.$router);
                                }});
                            }else{
                                that.$vux.alert.show(data.message);
                            }
                        },
                        message:''
                    });
                } 
            },
            validate(obj){
                if(obj.name=='name'){
                    this.name_status = obj.valid;
                }
                if(obj.name=='phone'){
                    console.log(obj.valid)
                    this.phone_status = obj.valid;
                }
                if(obj.name=='address'){
                    this.addr_status = obj.valid;
                }
                if(obj.name=='post_code'){
                    this.code_status = obj.valid;
                }
                if(this.name_status && this.phone_status && this.addr_status && this.code_status && this.addr_code_status){
                    this.color='primary';
                    this.disabled=false;
                }else{
                    this.color='default';
                    this.disabled=true;
                }
            },
            logHide(success){
                if(success){
                    this.addr_code_status = true;
                    if(this.name_status && this.phone_status && this.addr_status && this.code_status && this.addr_code_status){
                        this.color='primary';
                        this.disabled=false;
                    } 
                }
                
            },
            getname(txt){
                this.formData.addr_text=txt.split(' ');
            }
            
        },
        mounted: function () {
            this.$nextTick(function () {
                this.$vux.alert.hide();
                utils.MenuShare();
            })
        },
        components:{
            "cGroup":require('../../components/group/group.vue'),
            "cTopBack":require('../../components/x-top-back/x-top-back.vue'),
            "cInput":require('../../components/input/input.vue'),
            "cButton":require('../../components/button/button.vue'),
            "cCity":require('../../components/address/address.vue')
        }
    }
</script>
<style>
    .addradd .vux-popup-picker-value{
        white-space: nowrap; width:210px; overflow: hidden; display: block;
    }
</style>



